<template>
  <div style="height: 100vh">
    <template v-if="!isNull(url) && !isNull(userId)">
      <iframe name="console" :src="url" style="width: 100%; height: 100vh; border: none" />
    </template>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userId: "",
      url: "",
    };
  },
  mounted() {
    let user = localStorage.getItem("auth_user");
    if (!this.$tools.isNull(user)) {
      user = JSON.parse(user);
      this.userId = user.uid;
      this.url = "https://tunekey.app/screenshot/administration?cid=" + this.userId;
    }
  },
  methods: {
    isNull(obj) {
      return this.$tools.isNull(obj);
    },
  },
};
</script>

